
:root {
    --font-size-small: 14px;
    --font-size-medium: 16px;
    --font-family-main: sans-serif;
    --font-bold: 600;

    --transition:.4s;

    --color-white: #fff;
    --color-white: #000;
    --color-gray: #9c9c9c;
    --color-blue: #6a83fb;
    --color-light-gray: #dfe2df;

}

*, *:after, *:before{
    box-sizing: border-box;
}

img {
    display: block;
}